body {
background-color: #f2f2f2;
}
html, body {
height: 100%;
}

.split {
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;

overflow-y: auto;
overflow-x: hidden;
}

.gutter {
background-color: #eee;

background-repeat: no-repeat;
background-position: 50%;
}

.gutter.gutter-horizontal {
background-image: url('https://cdn.rawgit.com/nathancahill/Split.js/master/grips/vertical.png');
cursor: ew-resize;
}

.gutter.gutter-vertical {
background-image: url('grips/horizontal.png');
cursor: ns-resize;
}

.split.split-horizontal, .gutter.gutter-horizontal {
height: 100%;
float: left;
}

.editor {
padding-top: 50px;
height: 100%;
/*padding: 50px;*/
}

.editor-vertical {
height: 400px;
}

.editor-horizontal #code-col, #preview-col {
height: 100%;
float: left;
} 

.CodeMirror {
height: 100% !important;
}

#code-col {
position: relative;
}